  display: flex;
  justify-content: center;
  align-items: center;
}
.header-mobile span {
  display: block;
  width: 100%;
  height: 2rem;
  background: #ffffff;
  position: relative;
  border-radius: 2rem;
  transition: height 0.25s;
}
.header-mobile span:before {
  content: "";
  position: absolute;
  top: -5rem;
  left: 0;
  width: 100%;
  height: 2rem;
  background: #ffffff;
  border-radius: 2rem;
  transition: transform 0.25s;
}
.header-mobile span:after {
  border-radius: 2rem;
  content: "";
  position: absolute;
  bottom: -5rem;
  left: 0;
  width: 100%;
  height: 2rem;
  transition: transform 0.25s;
  background: #ffffff;
}

.header-mobile--active span {
  height: 0;
}
.header-mobile--active span:before {
  transform: rotate(45deg);
  top: -2rem;
}
.header-mobile--active span:after {
  bottom: 0;
  transform: rotate(-45deg);
}

